.container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  &__watch-field {
    background-image: url(../img/bg/Clock_background.png);
    background-size: $watch-size;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    box-shadow: -1px 1px 20px 10px rgba(0,0,0,0.25);
  }
}